div.recipe:hover .recipe-tool-row {
    display: block;
}

.recipe-tool-row {
    display: none;
    height: 0px;
}

.recipe {
    border-radius: 0;

    > span {
        display: inline;
    }

    > .panel-heading {
        text-align: center;
        border-radius: 0;
    }

    > .panel-footer {
        border-radius: 0;
        text-align: center;
    }

    .inset-image {
        display: block;
        margin: auto auto;
        width: 128px;
        height: 128px;
        background: url(https://openclipart.org/image/2400px/svg_to_png/172315/cupboard-short.png);
        background-size: 128px 128px;
        background-repeat: no-repeat;
    }
}

.recipe-red {
    border-color: indianred;

    > .panel-heading {
        background-color: indianred;
        border-color: indianred;
        color: white;
    }

    > .panel-body {
        background-color: #fff3f3;
    }

    > .panel-footer {
        background-color: indianred;
        color: white;
    }
}

.recipe-green {
    border-color: forestgreen;

    > .panel-heading {
        background-color: forestgreen;
        border-color: forestgreen;
        color: white;
    }

    > .panel-body {
        background-color: #f3fff3;
    }

    > .panel-footer {
        background-color: forestgreen;
        color: white;
    }
}

.recipe-purple {
    border-color: #aabbee;

    > .panel-heading {
        background-color: #aabbee;
        border-color: #aabbee;
        color: white;
    }

    > .panel-body {
        background-color: #f3f3ff;
    }

    > .panel-footer {
        background-color: #aabbee;
        color: white;
    }
}

.recipe-white {
    border-color: #333333;

    > .panel-heading {
        background-color: whitesmoke;
        border-color: #333333;
        color: #333333;
    }

    > .panel-body {
        background-color: white;
    }

    > .panel-footer {
        background-color: whitesmoke;
        color: #333333;
    }
}


